import React from 'react'
import styles from '../login.module.css'
import VirtualaiLogo from '../../../assets/icons/VirtualAiLogo.png'

function ForgotPasswordComponent(props) {
  const handleResetPassword = () => {}
  const showLoginComponent = () => {
    props.showLoginComponent()
  }
  return (
    <div>
      <img
        src={VirtualaiLogo}
        alt="VirtualAiLogo"
        className={styles.vaiLoginLogo}
      />
      <p className={styles.vaiTitleText}>Forgot Password?</p>
      <p className={styles.vaiDescription}>
        Enter the email address associated with your account and we'll send you
        the link for resetting your password.
      </p>
      <input
        type="text"
        className={styles.vaiLoginEmailInput}
        placeholder="Email"
      />
      <button
        className={styles.vaiResetPasswordButton}
        onClick={handleResetPassword}
      >
        Reset Password
      </button>
      <p
        className={styles.vaiBackText}
        onClick={showLoginComponent}
        style={{ cursor: 'pointer' }}
      >
        Back
      </p>
    </div>
  )
}

export default ForgotPasswordComponent
