import React, { useState } from 'react'
import { useHistory } from 'react-router-dom'
import authService from '../../core/auth.service'
import styles from './profile.module.css'
import profilePhoto from '../../assets/imgs/profile.png'
import chevIcon from '../../assets/icons/Chevron-Right.png'
import hamburgerMenuIcon from '../../assets/icons/Hamburger-Menu.png'
import hamburgerMenuToggledIcon from '../../assets/icons/Hamburger-Menu-Toggled.png'
import horizontalEllipsisIcon from '../../assets/icons/Horizontal-Ellipsis.png'
import signOutIcon from '../../assets/icons/Sign-Out.png'

function ProfileComponent(props) {
  const history = useHistory()
  const [sidebarState, setSidebarState] = useState(false)

  const toggleSidebar = () => {
    setSidebarState(!sidebarState)
    props.toggleSidebar(!sidebarState)
  }

  const handleLogout = () => {
    let path = '/login'
    authService.logout()
    history.push(path)
  }

  return (
    <>
      <div className="row mb-4 pb-1">
        <div className="col-12">
          {sidebarState ? (
            <img
              src={hamburgerMenuToggledIcon}
              alt="HamburgerMenuToggledIcon"
              className="mx-4"
              onClick={toggleSidebar}
              style={{ cursor: 'pointer' }}
            />
          ) : (
            <img
              src={hamburgerMenuIcon}
              alt="HamburgerMenuIcon"
              className="float-end"
              onClick={toggleSidebar}
              style={{ cursor: 'pointer' }}
            />
          )}
        </div>
      </div>
      <div className="mb-3 px-3 d-flex position-relative">
        <div className="position-relative">
          <div
            className={`${styles.vaiNotification} ${
              sidebarState ? styles.vaiShrinkNotification : null
            }`}
          >
            <span>2</span>
          </div>
          <span
            className={`${styles.vaiStatus} ${
              sidebarState ? styles.vaiShrinkStatus : null
            }`}
          ></span>
          <img
            src={profilePhoto}
            className={`${styles.vaiProfilePhoto} ${
              sidebarState ? styles.vaiShrinkPhoto : null
            }`}
            alt="ProfilePhoto"
          />
        </div>
        <div
          className={`${
            sidebarState ? styles.vaiHide : null
          } position-relative`}
          style={{ marginLeft: '15px' }}
        >
          <p className={styles.vaiUsername}>
            Ronald Madrid
            <br /> <span className={styles.vaiUserTitle}>.NET Developer</span>
          </p>
        </div>
        <img
          src={chevIcon}
          alt="ChevUpIcon"
          className={`${styles.vaiChevUp} ${
            sidebarState ? styles.vaiHide : null
          }`}
        />
        <img
          src={chevIcon}
          alt="ChevDownIcon"
          className={`${styles.vaiChevDown} ${
            sidebarState ? styles.vaiHide : null
          }`}
        />
      </div>
      <div
        className="text-center"
        style={{ display: sidebarState ? null : 'none', marginTop: '-15px' }}
      >
        <img src={horizontalEllipsisIcon} alt="Horizontal-Ellipsis" />
      </div>
      <div
        className="px-3 mx-1 position-relative"
        style={{ display: sidebarState ? 'none' : 'block', cursor: 'pointer' }}
        onClick={handleLogout}
      >
        <img src={signOutIcon} alt="Horizontal-Ellipsis" />
        <span className={styles.vaiSignOutText}>Sign Out</span>
      </div>
      <br />
      <br />
    </>
  )
}

export default ProfileComponent
