import React, { useState } from 'react'
import FooterComponent from '../../shared/footer/footer.component'
import ForgotPasswordComponent from './forgot-password/forgotPassword.component'
import styles from './login.module.css'
import SignInComponent from './sign-in/signIn.component'

function LoginComponent(props) {
  const showForgotPasswordComponent = () => {
    setComponent(
      <ForgotPasswordComponent showLoginComponent={showLoginComponent} />
    )
  }

  const loginComponent = (
    <SignInComponent
      showForgotPasswordComponent={showForgotPasswordComponent}
    />
  )

  const showLoginComponent = () => {
    setComponent(loginComponent)
  }

  const [component, setComponent] = useState(loginComponent)

  return (
    <div className={styles.vaiLoginBody}>
      <div className={styles.vaiLoginPanel}>
        <div className="container">
          <div className="row py-3">
            <div className="col-12 text-center py-4">{component}</div>
          </div>
        </div>
      </div>
      <FooterComponent />
    </div>
  )
}

export default LoginComponent
