import axios from 'axios'

class AuthService {
  isAuthenticated = () => {
    if (localStorage.getItem('userIdentity')) {
      alert(true)
      return true
    } else {
      alert(false)
      return false
    }
  }

  setUserIdentity = (userIdentity) => {
    localStorage.setItem('userIdentity', JSON.stringify(userIdentity))
  }

  login = (loginInfo) => {
    const url = process.env.REACT_APP_HOST_URL + 'Auth/Login'
    return axios.post(url, loginInfo)
  }

  logout = () => {
    localStorage.removeItem('userIdentity')
  }

  getToken = () => {
    const userIdentity = JSON.parse(localStorage.getItem('userIdentity'))
    if (!userIdentity) return null
    return userIdentity.token
  }
}
const authService = new AuthService()
export default authService
