import React from 'react'
import styles from './notFound.module.css'
import cloud from '../../assets/icons/Cloud.png'
import FooterComponent from '../../shared/footer/footer.component'
import { useHistory } from 'react-router-dom'

function NotFoundComponent() {
  const history = useHistory()

  const navigateToHome = () => {
    let path = `/encore-monitoring`
    history.push(path)
  }

  return (
    <div
      style={{
        height: '100vh',
        width: '100vw',
        minHeight: '700px',
        position: 'relative',
      }}
    >
      <div className={`${styles.vai404Page} text-white text-center`}>
        <h1>VIRTUAL AI</h1>
        <p className={styles.vai404Description1}>Something's wrong here...</p>
        <p className={styles.vai404Description2}>
          We can't find the page you're looking for.
        </p>
        <img className={styles.vai404Cloud} src={cloud} alt="Cloud-Icon" />
        <br />
        <button className={styles.vai404Button} onClick={navigateToHome}>
          Home
        </button>
      </div>
      <FooterComponent />
    </div>
  )
}

export default NotFoundComponent
