import React, { useState } from 'react'
import styles from './table.module.css'
import chev from '../../assets/icons/Chevron-Right.png'
import filter from '../../assets/icons/Filter.png'
import arrowUp from '../../assets/icons/Arrow-Up.png'
import triangle from '../../assets/icons/Triangle-Down.png'

function TableComponent() {
  const monitorings = [
    {
      id: 1,
      client: 'ITV',
      title: 'ITV - Substitle QoS - V..',
      worker: 'ITV-PRD-LD-04',
      started: '18/08/2021 08:44',
      ended: '18/08/2021 08:50',
      duration: '0:36',
      status: 'Running',
    },
    {
      id: 2,
      client: 'ITV',
      title: 'CRR - Buyer Approval',
      worker: 'VAI-Mini-P005',
      started: '18/08/2021 08:44',
      ended: '18/08/2021 08:50',
      duration: '1:25',
      status: 'Success',
    },
    {
      id: 3,
      client: 'SKY',
      title: 'SKY - Vod Promo',
      worker: 'VAI-Mini-PO01',
      started: '18/08/2021 08:44',
      ended: '18/08/2021 08:50',
      duration: '3:21',
      status: 'Running',
    },
    {
      id: 4,
      client: 'ITV',
      title: 'ITV - Product Catalo...',
      worker: 'ITV-PRD-LD-03',
      started: '18/08/2021 08:44',
      ended: '18/08/2021 08:50',
      duration: '0:43',
      status: 'Stopped',
    },
    {
      id: 5,
      client: 'SKY',
      title: 'SKY - DPL - Sky Spo..',
      worker: 'ITV-PRD-LD-02',
      started: '18/08/2021 08:44',
      ended: '18/08/2021 08:50',
      duration: '1:25',
      status: 'Success',
    },
    {
      id: 6,
      client: 'SKY',
      title: 'SKY - DPL - Sky Spo..',
      worker: 'WD013647',
      started: '18/08/2021 08:44',
      ended: '18/08/2021 08:50',
      duration: '2:35',
      status: 'Success',
    },
    {
      id: 7,
      client: 'SKY',
      title: 'SKY - BSS - Channel L..',
      worker: 'VAI-Mini-P010',
      started: '18/08/2021 08:44',
      ended: '18/08/2021 08:50',
      duration: '10:45',
      status: 'Running',
    },
    {
      id: 8,
      client: 'ITV',
      title: 'CRR - Buyer Reminders',
      worker: 'VAI-Mini-PO16',
      started: '18/08/2021 08:44',
      ended: '18/08/2021 08:50',
      duration: '2:25',
      status: 'Failed',
    },
    {
      id: 9,
      client: 'ITV',
      title: 'ITV - Silvermouse Da..',
      worker: 'ITV-PRD-LD-03',
      started: '18/08/2021 08:44',
      ended: '18/08/2021 08:50',
      duration: '0:35',
      status: 'Stopped',
    },
    {
      id: 10,
      client: 'ITV',
      title: 'ITV - PRS Data Extract',
      worker: 'VAI-Mini-PO12',
      started: '18/08/2021 08:44',
      ended: '18/08/2021 08:50',
      duration: '0:54',
      status: 'Failed',
    },
    {
      id: 11,
      client: 'ITV',
      title: 'ITV - Subtitle QoS - Verify',
      worker: 'ITV-PRD-LD-04',
      started: '18/08/2021 08:44',
      ended: '18/08/2021 08:50',
      duration: '18:43',
      status: 'Success',
    },
    {
      id: 12,
      client: 'ITV',
      title: 'ITV - Subtitle QoS - Verify',
      worker: 'ITV-PRD-LD-04',
      started: '18/08/2021 08:44',
      ended: '18/08/2021 08:50',
      duration: '18:43',
      status: 'Queued',
    },
  ]
  const buttons = [
    {
      buttonId: 1,
      buttonText: 1,
    },
    {
      buttonId: 2,
      buttonText: 2,
    },
    {
      buttonId: 3,
      buttonText: 3,
    },
    {
      buttonId: 4,
      buttonText: 4,
    },
    {
      buttonId: 6,
      buttonText: 5,
    },
  ]
  const [selectedItem, setSelectedItem] = useState({
    id: null,
    client: '',
    title: '',
    worker: '',
    started: '',
    ended: '',
    duration: '',
    status: '',
  })
  const [currentPage, setNewCurrentPage] = useState(1)

  const handlePreviousPage = () => {
    if (currentPage > 1) setNewCurrentPage(currentPage - 1)
  }

  const setCurrentPage = (page) => {
    setNewCurrentPage(page)
  }

  const handleNextPage = () => {
    if (currentPage < buttons.length) setNewCurrentPage(currentPage + 1)
  }

  const selectItem = (id) => {
    setSelectedItem(monitorings[id])
  }

  return (
    <>
      <div className="overflow-auto">
        <table>
          <thead>
            <tr>
              <th className={styles.vaiThClient}>
                CLIENT <img src={filter} alt="FilterIcon1" className="mx-2" />
              </th>
              <th className={styles.vaiThTitle}>
                TITLE <img src={filter} alt="FilterIcon2" className="mx-2" />
              </th>
              <th className={styles.vaiThWorker}>
                WORKER <img src={filter} alt="FilterIcon3" className="mx-2" />
              </th>
              <th className={styles.vaiThStarted}>
                STARTED <img src={filter} alt="FilterIcon4" className="mx-2" />
                <img src={arrowUp} alt="ArrowUpIcon" />
              </th>
              <th className={styles.vaiThEnded}>
                ENDED <img src={filter} alt="FilterIcon5" className="mx-1" />
              </th>
              <th className={styles.vaiThDuration}>
                DURATION <img src={filter} alt="FilterIcon6" className="mx-2" />
              </th>
              <th className={styles.vaiThStatus}>
                STATUS <img src={filter} alt="FilterIcon" className="mx-2" />
              </th>
            </tr>
          </thead>
          <tbody className={styles.vaiTbody}>
            {monitorings.map((monitoring, index) => {
              const successClassName =
                monitoring.status === 'Success' ? styles.vaiSuccessStatus : null
              const runningClassName =
                monitoring.status === 'Running' ? styles.vaiRunningStatus : null
              const failedClassName =
                monitoring.status === 'Failed' ? styles.vaiFailedStatus : null
              const stoppedClassName =
                monitoring.status === 'Stopped' ? styles.vaiStoppedStatus : null
              const queuedClassName =
                monitoring.status === 'Queued' ? styles.vaiQueuedStatus : null
              return (
                <tr
                  className={
                    selectedItem.id === monitoring.id
                      ? styles.vaiActiveRow
                      : null
                  }
                  key={index}
                  onClick={() => selectItem(index)}
                  style={{ cursor: 'pointer' }}
                >
                  <td>{monitoring.client}</td>
                  <td>{monitoring.title}</td>
                  <td>{monitoring.worker}</td>
                  <td>{monitoring.started}</td>
                  <td>{monitoring.ended}</td>
                  <td>{monitoring.duration}</td>
                  <td>
                    <div
                      className={`${successClassName} ${runningClassName} ${failedClassName} ${stoppedClassName} ${queuedClassName}`}
                    >
                      {monitoring.status}
                    </div>
                  </td>
                </tr>
              )
            })}
          </tbody>
        </table>
      </div>
      <div className={styles.vaiTableFooter}>
        <div className="row">
          <div className="col-12">
            <label className={styles.vaiLabel}>Listing</label>
            <button className={styles.vaiListButton}>
              12{' '}
              <img
                src={triangle}
                alt="Triangle-Icon"
                style={{ marginLeft: '3px', marginBottom: '2px' }}
              />
            </button>
            <label className={styles.vaiLabel}>
              entries, showing 1 to 12 of 342
            </label>

            <div
              className="btn-group float-end"
              role="group"
              aria-label="Basic example"
            >
              <button
                type="button"
                className={styles.vaiPaginationBtn}
                onClick={handlePreviousPage}
              >
                <img
                  src={chev}
                  alt="Paginate-Left"
                  className={styles.vaiChevLeftIcon}
                />
              </button>
              {buttons.map((button, index) => {
                return (
                  <button
                    key={index}
                    type="button"
                    onClick={() => setCurrentPage(button.buttonId)}
                    className={`${styles.vaiPaginationBtn} ${
                      button.buttonId === currentPage
                        ? styles.vaiPaginationActiveBtn
                        : null
                    }`}
                  >
                    {button.buttonText}
                  </button>
                )
              })}
              <button
                type="button"
                className={styles.vaiPaginationBtn}
                onClick={handleNextPage}
              >
                <img
                  src={chev}
                  alt="Paginate-Right"
                  className={styles.vaiChevRightIcon}
                />
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default TableComponent
